<template>
    <div>
        <div class="row" v-if="product!=null">
			<div class="col-12 p-1 text-uppercase">
				<b>{{$route.params.kategori}}</b>
			</div>
		</div>
		<div class="row">
			<img v-if="product==null" :src="$store.state.url_images+'loading.svg'" class="img-fluid d-flex mx-auto" style="height: 100px">
		</div>
		<div class="row" v-show="product!=null">
			<div class="col-4 p-1" v-for="(product, key) in product" :key="'product'+key">
				<router-link :to="{name:'Product',params:{kategori:product.kategori, kategori_id:product.id_kategory_barang, product_id:product.id_barang,product:product.seo_nama_barang}}">
					<div class="card product">
						<img v-if="product.depan!=null&&product.depan!=''" class="lazy img-fluid card-img-top" :src="$store.state.url_images+'loading_post_produk.svg'" :data-src="$store.state.url_product+product.depan" :alt="product.nama_barang">
						<img v-else class="img-fluid card-img-top" :src="$store.state.url_images+'default_image.jpeg'" :alt="product.nama_barang">
						<div class="card-body">
							<p class="product-name small" :title="product.nama_barang">{{product.nama_barang}}</p>
							<strong class="small font-weight-bold text-success" v-if="product.harga_barang!=0 & product.harga_modal!=0">
								<template v-if="product.harga_barang!=0 & product.harga_modal!=0">
								Rp{{toCurrency(product.harga_barang)}}
								</template>
								<template v-if="product.harga_barang==0 & product.harga_modal!=0">
								Rp{{toCurrency(product.harga_modal)}}
								</template>
							</strong>
							<br>
							<router-link :to="{name:'Store',params:{store:product.penjual_model.seo_nama_toko}}" class="small text-muted sell-name">{{product.penjual_model.nama_toko}}</router-link>
						</div>
					</div>
				</router-link>
			</div>
		</div>
    </div>
</template>
<script>
export default {
    name:"HomeWithProduct",
    data(){
        return {
			busy:false,
			// product:[]
        }
	},
	computed:{
		product(){
			return this.$store.state.product.product
		}
	},
    async created(){
		if(this.$route.params.kategori_id!=this.$store.state.product.last_category){
			this.$store.dispatch('reset_product')
		}
		if(this.$store.state.product.product==null){
			await this.data_product();
		}

		window.addEventListener('scroll', this.loadMore)
	},
	destroyed () {
		window.removeEventListener('scroll',this.loadMore)
	},
    async updated(){
        await this.lazyLoad()
	},
    methods:{
		loadMore(){
			if(!this.busy){
				
				var d = document.documentElement;
				var offset = d.scrollTop + window.innerHeight;
				var height = d.offsetHeight;
			
				if (offset >= height-50) {
					this.data_product();
				}
			}
		},
        lazyLoad() {
			var lazyloadImages;

			if ("IntersectionObserver" in window) {
				lazyloadImages = document.querySelectorAll(".lazy");
				var imageObserver = new IntersectionObserver(function (entries) {
					entries.forEach(function (entry) {
						if (entry.isIntersecting) {
							var image = entry.target;
							image.src = image.dataset.src;
							image.classList.remove("lazy");
							imageObserver.unobserve(image);
						}
					});
				});

				lazyloadImages.forEach(function (image) {
					imageObserver.observe(image);
				});
            }
		},
        toCurrency(angka) {
			angka += '';
			// angka = angka.replace(".",",");
			var x = angka.split('.');
			var x1 = x[0];
			var x2 = x.length > 1 ? '.' + x[1] : '';
			var rgx = /(\d+)(\d{3})/;
			while (rgx.test(x1)) {
				x1 = x1.replace(rgx, '$1' + '.' + '$2');
			}
			return x1 + x2;
		},
        data_product(){
			this.busy = true;
			let banyakProduct = this.$store.getters.product_length;
			var url = "";
			var headers = {};
            if(this.$store.state.login.ifLogin){
				url = this.$store.state.url_api + "product?category="+this.$route.params.kategori_id+"&cur="+banyakProduct;
				headers = {
					"Authorization": "Bearer " + localStorage.token,
				};
					
            }else{
                url = this.$store.state.url_api + "product?&category="+this.$route.params.kategori_id+"&cur="+banyakProduct
				headers = {
					"Content-Type":"application/json" ,
				};
			}	
			this.axios.get(url,{
				headers: headers,
			}).then(response => {
				response.data.sort(function(a,b){
					return b.analytics_barang_model.cta-a.analytics_barang_model.cta || b.id_barang-a.id_barang;
				})
				this.$store.dispatch('set_product', response.data);
				this.$store.dispatch('set_last_category', this.$route.params.kategori_id);
				this.busy = false;
			})
        }
    }
}
</script>